import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
export default {
  name: "ProdModal",
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.prodList();
      }
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "prod_name",
      searchText: "",
      columnDefs: [{
        field: "prod_code",
        headerName: "제품코드",
        flex: 2
      }, {
        field: "prod_name",
        headerName: "제품명",
        flex: 3
      }, {
        field: "unit",
        headerName: "단위",
        flex: 2
      }, {
        field: "by_unit_number",
        headerName: "단위 당 갯수",
        flex: 3
      }],
      gridOptions: {
        domLayout: "autoHeight",
        // singleClickEdit: true,               // 셀 편집 ON
        // suppressRowClickSelection: true,     // 행 클릭 선택 OFF
        suppressCellFocus: true,
        // 셀 포커스 OFF
        pagination: true,
        // 페이징 ON
        paginationPageSize: 5,
        // 한 페이지 보여질 행 수
        paginationPageSizeSelector: false,
        //paseSize 선택란 삭제
        defaultColDef: {
          // 열(컬럼) 기본 설정
          suppressMovable: true,
          // 컬럼을 드래그하여 이동하지 못하게
          resizable: false,
          // 컬럼 크기 조절 못하게
          sortable: false,
          // 정렬 못하게
          cellStyle: {
            textAlign: "center"
          } // 텍스트를 가운데 정렬
        }
      }
    };
  },
  mounted() {
    // 제품 목록
    this.prodList();
  },
  methods: {
    // 모달창 닫기 이벤트
    close() {
      this.$emit("close");
    },
    // 제품 목록 조회 API
    prodList() {
      axios.get('/api/work/plan/prodList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    // 제품 목록 검색
    async searchProd() {
      await axios.get(`/api/work/plan/prodList`, {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(err => console.error(err));
    },
    // 그리드 행 클릭 메소드
    onRowClicked(event) {
      this.$emit('selectProd', event.data);
      this.close();
    }
  }
};