import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
export default {
  name: "ComModal",
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "company_name",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "company_code",
        headerName: "업체코드",
        flex: 1
      }, {
        field: "company_name",
        headerName: "업체명",
        flex: 1
      }, {
        field: "tel",
        headerName: "전화번호",
        flex: 1
      }, {
        field: "address",
        headerName: "주소",
        flex: 1
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.matList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    matList() {
      axios.get('/api/sales/comList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchMaterials() {
      axios.get('/api/sales/comList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    clicked(event) {
      this.$emit('selectCom', event.data);
      this.close();
    }
  }
};